.bullet {
    padding: 25px 10px;
    display: block;
}

.left {
    text-align: left;
}

.graphic {
    width: 100px;
}

.build {
    font-weight: 400;
    width: 300px;
}

.description {
    font-weight: 300;
    margin-bottom: 25px;
    width: 300px;
}

.price {
    font-weight: 900;
}

.priceWrap {
    display: flex;
}

@media screen and (min-width: 1024px) {
    .bullet {
        padding: 50px 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .graphic {
        width: 300px;
    }

    .build {
        width: 300px;
    }

    .description {
        font-weight: 300;
        width: 300px;
    }
}

@media screen and (min-width: 1240px) {
    .container {
        display: flex;
        justify-content: center;
    }
    .bullet {
        width: 1000px;
        padding: 80px 10px;
    }

    .description {
        width: 400px;
    }

    .textWrapper {
        width: 400px;
    }

    .graphic {
        width: 400px;
    }
}

@media screen and (min-width: 1920px) {
    .bullet {
        width: 1100px;
        padding: 80px 10px;
    }
}

.reverse {
    text-align: -webkit-right;
    flex-direction: row-reverse;
}
